.tickets-list {
  width: 31.375rem;
  list-style: none;
  display: flex;
  flex-direction: column;
  align-items: center;

  &__ticket-info {
    width: 100%;
    margin: 0.625rem 0rem;

    &:first-of-type {
      margin-top: 0rem;
    }
    &:last-of-type {
      margin-bottom: 0rem;
    }
  }
}
